.form {
  @include flex-v-center;

  &-header {
    grid-column: 10 span;
    grid-row: 3;

    &__label {
      @include flex-v-center;

      position: relative;
      width: 100%;
      height: 100%;

      &--svg {
        position: absolute;
        left: 31px;
        margin-right: 6px;
        width: 20px;
        height: 20px;
        fill: var(--grey-color);
      }
    }

    .just-validate-error-label {
      position: absolute;
      left: 60px;
      top: 3px;
      font-weight: 400;
      font-size: 9px;
      line-height: normal;
      color: var(--secondary_shade-color) !important;
    }

    &__input {
      padding-left: 60px;
      padding-right: 36px;
      outline: none;
      border-radius: 10px;
      width: 100%;
      height: 100%;
      background-color: var(--grey_light-color);
      transition: background-color 0.3s ease-in-out;

      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: var(--grey-color);
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 2px solid var(--primary-color);
        background-color: var(--pink_light-color);
      }

      &:focus + .form-header__label--svg {
        fill: var(--primary-color);
      }

      &:hover {
        outline: none;
        background-color: var(--md_grey-color);
      }
    }

    &__btn-search {
      position: absolute;
      right: 36px;
      border-radius: 100px;
      border: 2px solid var(--primary-color);
      width: 32px;
      height: 32px;
      transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
        background-color: var(--primary-color);
      }

      &:active {
        outline: none;
        border-color: var(--primary_shade-color);
        background-color: var(--primary_shade-color);
      }

      &:focus-visible:not(:hover):not(:active) .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &:hover .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &:active .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &--svg {
        width: 8px;
        height: 28px;
        stroke-width: 2px;
        stroke: var(--primary-color);
        fill: transparent;
        transition: stroke 0.3s ease-in-out;
      }
    }
  }
}
